<template>
  <div v-loading="loading" element-loading-text="拼命加载中">
    <div class="handle-box">
      <div class="btn">
        <el-button type="primary" @click="addSchool">添加学校</el-button>
      </div>
    </div>

    <el-dialog
      title="添加学校"
      :visible.sync="dialogSchool"
      :close-on-click-modal="false"
    >
      <div class="box">
        <el-form :model="form" label-width="100px">
          <el-form-item label="学校名称">
            <el-input
              v-model="form.name"
              placeholder="请输入学校名称"
            ></el-input>
          </el-form-item>
          <el-form-item label="学校地址">
            <el-input
              type="textarea"
              v-model="form.address"
              placeholder="请输入学校地址"
            ></el-input>
          </el-form-item>
          <el-form-item label="人脸库(进)">
            <el-input
              v-model="form.in_group"
              placeholder="请输入人脸库(进)"
            ></el-input>
          </el-form-item>
          <el-form-item label="人脸库(出)">
            <el-input
              v-model="form.out_group"
              placeholder="请输入人脸库(出)"
            ></el-input>
          </el-form-item>
          <el-form-item label="收费模式">
            <el-input v-model="form.mode"></el-input>
          </el-form-item>
          <!-- 	<el-form-item label="用户名">
						<el-input v-model="form.user.username"></el-input>
					</el-form-item>
					<el-form-item label="密码">
						<el-input v-model="form.user.password"></el-input>
          </el-form-item>-->
          <div v-for="(item, index) in form.grades" :key="index">
            <el-form-item label="年级选择">
              <el-select v-model="item.title" placeholder="请选择年级">
                <el-option
                  v-for="selectItem in grade"
                  :key="selectItem.value"
                  :label="selectItem.label"
                  :value="selectItem.value"
                ></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="班级">
              <el-transfer
                v-model="item.classes"
                :data="classList"
                :titles="['班级', '选中班级']"
                :button-texts="['取消', '确定']"
              ></el-transfer>
            </el-form-item>
          </div>
          <el-form-item label="操作">
            <el-button type="success" @click="addGrade">添加年级</el-button>
            <el-button type="success" @click="delGrade">删除年级</el-button>
          </el-form-item>
          <div class="submit">
            <el-form-item>
              <el-button type="primary" @click="newSchool">提交</el-button>
            </el-form-item>
          </div>
        </el-form>
      </div>
    </el-dialog>

    <el-table
      :data="tableDate"
      border
      :header-cell-style="{ background: '#f0f0f0' }"
      max-height="620"
    >
      <el-table-column prop="id" label="学校ID"></el-table-column>
      <el-table-column prop="name" label="学校名称"></el-table-column>
      <el-table-column prop="address" label="学校地址"></el-table-column>
      <el-table-column prop="created_at" label="创建时间"></el-table-column>
      <el-table-column label="操作" width="400px">
        <template slot-scope="scope">
          <el-dropdown>
            <el-button type="primary">
              操作
              <i class="el-icon-arrow-down el-icon--right"></i>
            </el-button>
            <el-dropdown-menu slot="dropdown">
              <el-dropdown-item>
                <el-button
                  size="mini"
                  type="primary"
                  @click="handleEdit(scope.$index, scope.row)"
                  >编辑</el-button
                >
              </el-dropdown-item>
              <el-dropdown-item>
                <el-button
                  size="mini"
                  type="primary"
                  @click="handleShowUser(scope.$index, scope.row)"
                  >查看用户</el-button
                >
              </el-dropdown-item>
              <el-dropdown-item>
                <el-button
                  size="mini"
                  type="primary"
                  @click="handleShowFinancial(scope.$index, scope.row)"
                  >查看财务</el-button
                >
              </el-dropdown-item>
              <el-dropdown-item>
                <el-button
                  size="mini"
                  type="primary"
                  @click="handleFace(scope.$index, scope.row)"
                  >全库推送</el-button
                >
              </el-dropdown-item>
              <el-dropdown-item>
                <el-button
                  type="danger"
                  size="mini"
                  @click="handleDelete(scope.$index, scope.row)"
                  >删除学校</el-button
                >
              </el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
        </template>
      </el-table-column>
    </el-table>

    <el-dialog
      title="查看学校所有"
      :visible.sync="dialogUser"
      width="80%"
      @close="closeShowUser"
    >
      <div class="box">
        <div class="btn">
          <el-select v-model="user" @change="handleUser" placeholder="请选择">
            <el-option
              v-for="item in userList"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            ></el-option>
          </el-select>
        </div>

        <template v-if="user == '用户'">
          <el-table
            :data="userData"
            border
            :header-cell-style="{ background: '#f0f0f0' }"
            max-height="620"
          >
            <el-table-column prop="id" label="用户ID"></el-table-column>
            <el-table-column prop="href" label="头像">
              <template slot-scope="scope">
                <img
                  :src="scope.row.href"
                  alt
                  style="max-width: 180px; max-height: 80px"
                />
              </template>
            </el-table-column>
            <el-table-column prop="name" label="姓名"></el-table-column>
            <el-table-column prop="sex" label="性别">
              <template slot-scope="scope">
                <span v-text="scope.row.sex == 1 ? '男' : '女'"></span>
              </template>
            </el-table-column>
            <el-table-column prop="phone" label="手机号码"></el-table-column>
            <el-table-column prop="worker" label="是否老师">
              <template slot-scope="scope">
                <span v-if="scope.row.worker == 0">否</span>
                <span v-else-if="scope.row.worker == 1">否</span>
                <span v-else-if="scope.row.worker == 2">是</span>
                <span v-else-if="scope.row.worker == 3">否</span>
              </template>
            </el-table-column>
            <el-table-column prop="worker" label="是否班主任">
              <template slot-scope="scope">
                <span v-if="scope.row.worker == 0">否</span>
                <span v-else-if="scope.row.worker == 1">否</span>
                <span v-else-if="scope.row.worker == 2">是</span>
                <span v-else-if="scope.row.worker == 3">否</span>
              </template>
            </el-table-column>
            <el-table-column prop="worker" label="是否教职工">
              <template slot-scope="scope">
                <span v-if="scope.row.worker == 0">否</span>
                <span v-else-if="scope.row.worker == 1">否</span>
                <span v-else-if="scope.row.worker == 2">是</span>
                <span v-else-if="scope.row.worker == 3">否</span>
              </template>
            </el-table-column>
          </el-table>
          <div class="block">
            <el-pagination
              @current-change="userChange"
              :current-page.sync="userCurrent"
              :page-sizes="[10, 20, 30, 40, 50]"
              :page-size="userSize"
              layout="sizes, prev, pager, next, jumper"
              :total="userTotal"
              @size-change="userSizeChange"
            ></el-pagination>
          </div>
        </template>
        <template v-if="user == '学生'">
          <el-table
            :data="studentData"
            border
            :header-cell-style="{ background: '#f0f0f0' }"
            max-height="620"
          >
            <el-table-column prop="id" label="学号"></el-table-column>
            <el-table-column prop="name" label="姓名"></el-table-column>
            <el-table-column prop="sex" label="性别">
              <template slot-scope="scope">
                <span v-text="scope.row.sex == 1 ? '男' : '女'"></span>
              </template>
            </el-table-column>
            <el-table-column prop="age" label="年龄"></el-table-column>
            <el-table-column prop="grades.title" label="年级"></el-table-column>
            <el-table-column prop="class.title" label="班级"></el-table-column>
            <el-table-column prop="face_id" label="生活头像">
              <template slot-scope="scope">
                <img
                  :src="scope.row.face_id"
                  alt
                  style="max-width: 180px; max-height: 80px"
                />
              </template>
            </el-table-column>
            <el-table-column prop="face_image" label="人脸头像">
              <template slot-scope="scope">
                <img
                  :src="scope.row.face_image"
                  alt
                  style="max-width: 180px; max-height: 80px"
                />
              </template>
            </el-table-column>
            <el-table-column
              prop="userInfo.user_id"
              label="默认家长ID"
            ></el-table-column>
            <el-table-column
              prop="userInfo.name"
              label="默认家长姓名"
            ></el-table-column>
            <el-table-column
              prop="userInfo.phone"
              label="默认家长手机"
            ></el-table-column>
          </el-table>
          <div class="block">
            <el-pagination
              @current-change="studentChange"
              :current-page.sync="studentCurrent"
              :page-sizes="[10, 20, 30, 40, 50]"
              :page-size="studentSize"
              layout="sizes, prev, pager, next, jumper"
              :total="studentTotal"
              @size-change="studentSizeChange"
            ></el-pagination>
          </div>
        </template>
        <template v-if="user == '教师'">
          <el-table
            :data="teacherData"
            border
            :header-cell-style="{ background: '#f0f0f0' }"
            max-height="620"
          >
            <el-table-column prop="id" label="用户ID"></el-table-column>
            <el-table-column prop="name" label="姓名"></el-table-column>
            <el-table-column
              prop="wxUser.nickname"
              label="昵称"
            ></el-table-column>
            <el-table-column prop="sex" label="性别">
              <template slot-scope="scope">
                <span v-text="scope.row.sex == 1 ? '男' : '女'"></span>
              </template>
            </el-table-column>
            <el-table-column prop="phone" label="手机号码"></el-table-column>

            <el-table-column prop="grades.title" label="年级">
              <template slot-scope="scope">
                <span
                  v-text="
                    scope.row.grades.title == null
                      ? '无'
                      : scope.row.grades.title
                  "
                ></span>
              </template>
            </el-table-column>
            <el-table-column prop="class.title" label="班级">
              <template slot-scope="scope">
                <span
                  v-text="
                    scope.row.class.title == null ? '无' : scope.row.class.title
                  "
                ></span>
              </template>
            </el-table-column>
            <el-table-column prop="href" label="人脸头像">
              <template slot-scope="scope">
                <img
                  :src="scope.row.href"
                  alt
                  style="max-width: 180px; max-height: 80px"
                />
              </template>
            </el-table-column>
            <el-table-column prop="worker" label="是否是班主任">
              <template slot-scope="scope">
                <span v-if="scope.row.worker == 0">否</span>
                <span v-else-if="scope.row.worker == 1">否</span>
                <span v-else-if="scope.row.worker == 2">是</span>
                <span v-else-if="scope.row.worker == 3">否</span>
              </template>
            </el-table-column>
            <el-table-column prop="state" label="审核状态">
              <template slot-scope="scope">
                <span v-if="scope.row.state == 1">待审核</span>
                <span v-else-if="scope.row.state == 2">已通过</span>
                <span v-else-if="scope.row.state == 3">未通过</span>
              </template>
            </el-table-column>
            <el-table-column
              prop="created_at"
              label="创建时间"
            ></el-table-column>
          </el-table>
          <div class="block">
            <el-pagination
              @current-change="teacherChange"
              :current-page.sync="teacherCurrent"
              :page-sizes="[10, 20, 30, 40, 50]"
              :page-size="teacherSize"
              layout="sizes, prev, pager, next, jumper"
              :total="teacherTotal"
              @size-change="teacherSizeChange"
            ></el-pagination>
          </div>
        </template>

        <template v-if="user == '访客'">
          <el-table
            :data="visitorData"
            border
            :header-cell-style="{ background: '#f0f0f0' }"
            max-height="620"
          >
            <el-table-column prop="id" label="访客ID"></el-table-column>
            <el-table-column prop="user_id" label="用户ID"></el-table-column>
            <el-table-column prop="name" label="姓名"></el-table-column>
            <el-table-column prop="phone" label="手机号"></el-table-column>
            <el-table-column prop="user_name" label="拜访人"></el-table-column>
            <el-table-column prop="reason" label="拜访理由"></el-table-column>
            <el-table-column prop="href" label="访客人脸">
              <template slot-scope="scope">
                <img
                  :src="scope.row.href"
                  alt
                  style="max-width: 180px; max-height: 80px"
                />
              </template>
            </el-table-column>
          </el-table>
          <div class="block">
            <el-pagination
              @current-change="visitorChange"
              :current-page.sync="visitorCurrent"
              :page-sizes="[10, 20, 30, 40, 50]"
              :page-size="visitorSize"
              layout="sizes, prev, pager, next, jumper"
              :total="visitorTotal"
              @size-change="visitorSizeChange"
            ></el-pagination>
          </div>
        </template>
      </div>
    </el-dialog>
    <el-dialog
      title="查看财务"
      :visible.sync="dialogFinancial"
      width="80%"
      @close="closeFinancial"
    >
      <div class="box">
        <div class="btn">
          <el-select
            v-model="financial"
            @change="handleFinancial"
            placeholder="请选择"
          >
            <el-option
              v-for="item in financialList"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            ></el-option>
          </el-select>
        </div>

        <div v-if="financial === '订单列表'">
          <el-table
            :data="orderData"
            border
            :header-cell-style="{ background: '#f0f0f0' }"
            max-height="620"
          >
            <el-table-column prop="school_id" label="学校ID"></el-table-column>
            <el-table-column prop="no" label="订单ID"></el-table-column>
            <el-table-column prop="user_id" label="用户ID"></el-table-column>
            <el-table-column prop="user_name" label="用户名"></el-table-column>
            <el-table-column prop="name" label="商品名称"></el-table-column>
            <el-table-column prop="price" label="商品价格"></el-table-column>
            <el-table-column prop="time" label="商品时长"></el-table-column>
            <el-table-column prop="status" label="订单状态">
              <template slot-scope="scope">
                <span v-if="scope.row.status == 1">提交</span>
                <span v-else-if="scope.row.status == 2">已付款</span>
                <span v-else-if="scope.row.status == 3">无效</span>
              </template>
            </el-table-column>
            <el-table-column
              prop="created_id"
              label="创建时间"
            ></el-table-column>
          </el-table>
          <div class="block">
            <el-pagination
              @current-change="orderChange"
              :current-page.sync="orderCurrent"
              :page-sizes="[10, 20, 30, 40, 50]"
              :page-size="orderSize"
              layout="sizes, prev, pager, next, jumper"
              :total="orderTotal"
              @size-change="orderSizeChange"
            ></el-pagination>
          </div>
        </div>
        <div v-if="financial === '财务统计'">
          <el-table
            :data="financialData"
            border
            :header-cell-style="{ background: '#f0f0f0' }"
            max-height="620"
          >
            <el-table-column prop="id" label="周总额"></el-table-column>
            <el-table-column prop="name" label="月总额"></el-table-column>
            <el-table-column prop="address" label="季度总额"></el-table-column>
            <el-table-column
              prop="created_at"
              label="上一季度总额"
            ></el-table-column>
            <el-table-column prop="id" label="年总额"></el-table-column>
          </el-table>
          <div class="block">
            <el-pagination
              @current-change="handleFinanceChange"
              :current-page.sync="currentFinancePage"
              :page-sizes="[10, 20, 30, 40, 50]"
              :page-size="financePageSize"
              layout="sizes, prev, pager, next, jumper"
              :total="financeTotalPage"
              @size-change="handleFinanceSizeChange"
            ></el-pagination>
          </div>
        </div>
      </div>
    </el-dialog>

    <!-- 删除提示框 -->
    <el-dialog
      :visible.sync="dialogDel"
      title="删除学校"
      width="20%"
      align="center"
      :close-on-click-modal="false"
    >
      <div style="font-size: 20px; margin-bottom: 30px">是否删除该学校</div>
      <span>
        <el-button type="primary" @click="toDel">删除</el-button>
        <el-button type="danger" @click="dialogDel = false">取消</el-button>
      </span>
    </el-dialog>

    <!-- 全库推送人脸 -->
    <el-dialog
      :visible.sync="dialogFace"
      title="全库推送人脸"
      width="20%"
      align="center"
      :close-on-click-modal="false"
    >
      <div style="font-size: 20px; margin-bottom: 30px">是否全库推送人脸</div>
      <span>
        <el-button type="primary" @click="pushFace">推送</el-button>
        <el-button type="danger" @click="dialogFace = false">取消</el-button>
      </span>
    </el-dialog>

    <div class="block">
      <el-pagination
        @current-change="currentChange"
        :current-page.sync="current"
        :page-sizes="[10, 20, 30, 40, 50]"
        :page-size="size"
        layout="sizes, prev, pager, next, jumper"
        :total="total"
        @size-change="handleSizeChange"
      ></el-pagination>
    </div>
  </div>
</template>

<script>
import API from "@/api/index.js";

export default {
  name: "school",
  data() {
    return {
      loading: true,
      // 添加学校
      dialogSchool: false,
      dialogDel: false,
      form: {
        name: "",
        address: "",
        in_group: "",
        out_group: "",
        mode: 1,
        user: {
          username: "",
          password: "",
        },
        grades: [],
        id: "",
      },
      grade: [
        {
          value: 1,
          label: "一年级",
        },
        {
          value: "二年级",
          label: "二年级",
        },
        {
          value: "三年级",
          label: "三年级",
        },
        {
          value: "四年级",
          label: "四年级",
        },
        {
          value: "五年级",
          label: "五年级",
        },
        {
          value: "六年级",
          label: "六年级",
        },
        {
          value: "初一",
          label: "初一",
        },
        {
          value: "初二",
          label: "初二",
        },
        {
          value: "初三",
          label: "初三",
        },
        {
          value: "高一",
          label: "高一",
        },
        {
          value: "高二",
          label: "高二",
        },
        {
          value: "高三",
          label: "高三",
        },
        {
          value: "小班",
          label: "小班",
        },
        {
          value: "中班",
          label: "中班",
        },
        {
          value: "大班",
          label: "大班",
        },
      ],
      classList: [],

      // 操作
      // 查看用户
      dialogUser: false,
      user: "访客",
      userList: [
        {
          value: 1,
          label: "用户",
        },
        {
          value: 2,
          label: "学生",
        },
        {
          value: 3,
          label: "教师",
        },
        {
          value: 4,
          label: "访客",
        },
      ],
      id: "",
      userData: [], // 用户列表
      userSize: 10,
      userTotal: 0,
      userCurrent: 1,

      teacherData: [], // 教师列表
      teacherSize: 10,
      teacherTotal: 0,
      teacherCurrent: 1,

      studentData: [], // 学生列表
      studentSize: 10,
      studentTotal: 0,
      studentCurrent: 1,

      visitorData: [], // 访客列表
      visitorSize: 10,
      visitorTotal: 0,
      visitorCurrent: 1,

      // 查看财务
      dialogFinancial: false,
      financial: "订单列表",
      financialList: [
        {
          value: 1,
          label: "订单列表",
        },
        {
          value: 2,
          label: "财务统计",
        },
      ],
      orderData: [],
      orderSize: 10, // 订单列表
      orderTotal: 0,
      orderCurrent: 1,
      financialData: [],
      financePageSize: 10, // 财务统计
      financeTotalPage: 0,
      currentFinancePage: 1,

      tableDate: [],

      // 分页
      current: 1,
      size: 10,
      total: 0,
      school_id: "",
      dialogFace: false,
    };
  },
  mounted() {
    this.generateData();
    this.getSchool();
  },
  methods: {
    // 获取学校列表
    getSchool() {
      var self = this;
      API.schools(self.current)
        .then((res) => {
          self.loading = false;
          self.tableDate = res.data;
          self.total = res.total;
        })
        .catch((err) => {
          self.loading = false;
        });
    },
    // 分页
    currentChange(val) {
      var self = this;
      self.current = val;
      self.loading = true;
      API.schools(val, self.size)
        .then((res) => {
          self.loading = false;
          self.tableDate = res.data;
          self.total = res.total;
        })
        .catch((err) => {
          self.loading = false;
        });
    },
    // 每页多少条
    handleSizeChange(val) {
      var self = this;
      self.size = val;
      self.loading = true;
      API.schools(self.current, val)
        .then((res) => {
          self.loading = false;
          self.tableDate = res.data;
          self.total = res.total;
        })
        .catch((err) => {
          self.loading = false;
        });
    },

    newSchool() {
      var self = this;
      self.form.grades.forEach((item) => {
        for (var i = 0; i < item.classes.length; i++) {
          item.classes[i] = {
            title: item.classes[i],
          };
        }
      });
      API.school(self.form).then((res) => {
        self.dialogSchool = false;
        self.getSchool();
        self.form = {
          name: "",
          address: "",
          in_group: "",
          out_group: "",
          mode: 1,
          user: {
            username: "",
            password: "",
          },
          grades: [],
        };
        self.$message.success("提交成功");
        self.current = 1;
      });
    },
    // 操作
    handleEdit(index, row) {
      var self = this;
      self.dialogSchool = true;
      self.form = {
        name: row.name,
        address: row.address,
        in_group: row.in_group,
        out_group: row.out_group,
        mode: row.mode,
        user: {
          username: "",
          password: "",
        },
        grades: [],
        id: row.id,
      };
    },
    handleShowUser(index, row) {
      var self = this;
      self.dialogUser = true;
      self.id = row.id;
    },
    closeShowUser() {
      var self = this;
      self.user = "访客";
      self.userSize = 10;
      self.teacherSize = 10;
      self.visitorSize = 10;
      self.studentSize = 10;
      self.userCurrent = 1;
      self.teacherCurrent = 1;
      self.visitorCurrent = 1;
      self.studentCurrent = 1;
      self.userData = [];
      self.studentData = [];
      self.teacherData = [];
      self.visitorData = [];
    },
    handleUser(value) {
      var self = this;
      switch (value) {
        case 1:
          self.$nextTick(() => {
            self.user = "用户";
            API.schoolUser(self.userCurrent, self.userSize, self.id).then(
              (res) => {
                self.userData = res.data;
                self.userTotal = res.total;
                self.$message.success("获取数据成功");
              }
            );
          });
          break;
        case 2:
          self.$nextTick(() => {
            self.user = "学生";
            API.studentInfo(
              self.studentCurrent,
              self.studentSize,
              self.id
            ).then((res) => {
              self.studentData = res.data;
              self.studentTotal = res.total;
              self.$message.success("获取数据成功");
            });
          });
          break;
        case 3:
          self.$nextTick(() => {
            self.user = "教师";
            API.teacherUser(
              self.teacherCurrent,
              self.teacherSize,
              self.id
            ).then((res) => {
              self.teacherData = res.data;
              self.teacherTotal = res.total;
              self.$message.success("获取数据成功");
            });
          });
          break;
        case 4:
          self.$nextTick(() => {
            self.user = "访客";
            API.visitors(self.visitorCurrent, self.visitorSize, self.id).then(
              (res) => {
                self.visitorData = res.data;
                self.visitorTotal = res.total;
                self.$message.success("获取数据成功");
              }
            );
          });
          break;
      }
    },
    // 用户分页
    userChange(val) {
      var self = this;
      self.userCurrent = val;
      API.schoolUser(val, self.userSize, self.id).then((res) => {
        self.userData = res.data;
        self.userTotal = res.total;
      });
    },
    // 当前分页
    userSizeChange(val) {
      var self = this;
      self.userSize = val;
      API.schoolUser(self.userCurrent, val, self.id).then((res) => {
        self.userData = res.data;
        self.userTotal = res.total;
      });
    },
    // 学生分页
    studentChange(val) {
      var self = this;
      self.studentCurrent = val;
      API.studentInfo(val, self.studentSize, self.id).then((res) => {
        self.studentData = res.data;
        self.studentTotal = res.total;
      });
    },
    // 当前分页
    studentSizeChange(val) {
      var self = this;
      self.studentSize = val;
      API.studentInfo(self.studentCurrent, val, self.id).then((res) => {
        self.studentData = res.data;
        self.studentTotal = res.total;
      });
    },
    // 老师分页
    teacherChange(val) {
      var self = this;
      self.teacherCurrent = val;
      API.teacherUser(val, self.teacherSize, self.id).then((res) => {
        self.teacherData = res.data;
        self.teacherTotal = res.total;
      });
    },
    // 当前分页
    teacherSizeChange(val) {
      var self = this;
      self.teacherSize = val;
      API.teacherUser(self.teacherCurrent, val, self.id).then((res) => {
        self.teacherData = res.data;
        self.teacherTotal = res.total;
      });
    },
    // 访客分页
    visitorChange(val) {
      var self = this;
      self.visitorCurrent = val;
      API.visitors(val, self.visitorSize, self.id).then((res) => {
        self.visitorData = res.data;
        self.visitorTotal = res.total;
      });
    },
    // 当前分页
    visitorSizeChange(val) {
      var self = this;
      self.visitorSize = val;
      API.visitors(self.visitorCurrent, val, self.id).then((res) => {
        self.visitorData = res.data;
        self.visitorTotal = res.total;
      });
    },

    handleShowFinancial(index, row) {
      var self = this;
      self.dialogFinancial = true;
      self.id = row.id;
      self.orderCurrent = 1;
      self.orderSize = 10;
    },

    handleFinancial(value) {
      var self = this;
      switch (value) {
        case 1:
          self.$nextTick(() => {
            self.financial = "订单列表";
            API.orders(1, 10, self.id).then((res) => {
              self.orderData = res.data;
              self.orderTotal = res.total;
              self.$message.success("获取数据成功");
            });
          });
          break;
        case 2:
          self.financial = "财务统计";
          API.orders(1, 10, self.id).then((res) => {
            self.financialData = res.data;
            self.financeTotalPage = res.total;
            self.$message.success("获取数据成功");
          });
          break;
      }
    },
    // 订单列表分页
    orderChange(val) {
      var self = this;
      self.orderCurrent = val;
      API.orders(val, self.orderSize, self.id).then((res) => {
        self.orderData = res.data;
        self.orderTotal = res.total;
      });
    },
    // 当前分页
    orderSizeChange(val) {
      var self = this;
      self.orderSize = val;
      API.orders(self.orderCurrent, val, self.id).then((res) => {
        self.orderData = res.data;
        self.orderTotal = res.total;
      });
    },
    // 财务统计分页
    handleFinanceChange(val) {
      var self = this;
      self.currentFinancePage = val;
      API.orders(val, self.financePageSize, self.id).then((res) => {
        self.financialData = res.data;
        self.financeTotalPage = res.total;
      });
    },
    // 当前分页
    handleFinanceSizeChange(val) {
      var self = this;
      API.orders(val, self.currentFinancePage, self.id).then((res) => {
        self.financialData = res.data;
        self.financeTotalPage = res.total;
      });
    },
    closeFinancial() {
      var self = this;
      self.financial = "订单列表";
      self.orderData = [];
      self.financialData = [];
    },
    handleDelete(index, row) {
      var self = this;
      console.log(row);
      self.school_id = row.id;
      self.dialogDel = true;
    },
    toDel() {
      var self = this;
      API.delSchool(self.school_id).then((res) => {
        self.dialogDel = false;
        self.$message.success("删除成功");
        self.getSchool();
      });
    },
    // 全库推送
    handleFace(index, row) {
      var self = this;
      self.dialogFace = true;
      self.school_id = row.id;
      console.log(row);
    },
    pushFace() {
      var self = this;
      const loading = this.$loading({
        lock: true,
        text: "推送中...",
        spinner: "el-icon-loading",
        background: "rgba(0, 0, 0, 0.7)",
      });
      API.pushSchFace(self.school_id)
        .then((res) => {
          loading.close();
          self.$message.success("推送成功");
          self.dialogFace = false;
        })
        .catch((err) => {
          console.log(err);
          loading.close();
        });
    },

    // 添加学校
    addSchool() {
      var self = this;
      self.dialogSchool = true;
      self.form = {
        name: "",
        address: "",
        in_group: "",
        out_group: "",
        mode: 1,
        user: {
          username: "",
          password: "",
        },
        grades: [],
      };
    },
    // 年级操作
    addGrade() {
      var self = this;
      self.form.grades.push({});
    },
    delGrade() {
      var self = this;
      self.form.grades.pop({});
    },
    generateData(_) {
      var self = this;
      var classData = [
        "一班",
        "二班",
        "三班",
        "四班",
        "五班",
        "六班",
        "七班",
        "八班",
        "九班",
        "十班",
      ];
      // var classData = ['小班', '1班','2班','3班','4班','5班']
      for (let i = 0; i < classData.length; i++) {
        self.classList.push({
          key: classData[i],
          label: classData[i],
        });
      }
      return self.classList;
    },
  },
};
</script>

